import React from "react";
import SkillContainer from "./skillContainer";
import "../css/projectBlock.min.css";

const ProjectBlock = (props) => {
    const showModal = () =>{
        console.log('execute!');
    }
	return (
		<>
			<div className='projectBlockContainer'>
				<div className='projectTitle'>
					{props.projectInfo.projectTitle}
				</div>
				<div className='projectImgWrapper'>
					<div className='projectImg'>
						<img
							src={require(`../image/portfolio/${props.projectInfo.folder}/${props.projectInfo.mainImg}`)}
							alt=''
						/>
					</div>
                    <div className="projectImgOverlay">
                        <button onClick={()=>showModal()}>View Details</button>
                    </div>
				</div>

				<div className='projectInfo'>
					<h4>
						<span className='contentInfoTitle'>Type:</span>
						<span className='contentInfoText'>
							{" "}
							{props.projectInfo.projectType}
						</span>
					</h4>
					<h4>
						<span className='contentInfoTitle'>Project: </span>
						<span className='contentInfoText'>
							{" "}
							{props.projectInfo.projectName}
						</span>
					</h4>
					<h4>
						<span className='contentInfoTitle'>
							Related technology:
						</span>
						<span className='skillInfoText'>
							{props.projectInfo.skills.map((skill, index) => (
								<SkillContainer
									key={`skill-${index}`}
									skill={skill}
								/>
							))}
						</span>
					</h4>
				</div>
			</div>
		</>
	);
};

export default ProjectBlock;
